import { Component, OnInit } from '@angular/core';
import { DialogService } from "ng2-bootstrap-modal";

/* Components */
import { GoogleContactModalComponent } from '../modals/google-contact-modal/google-contact-modal.component';
import { InvitationSocialComponent } from '../modals/invitation-social/invitation-social.component';

/* Services */
import { StatisticsService } from '../../Services/Statistics/statistics.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';
import { TokenService } from '../../Services/Authentication/token.service';
import { AccountService } from '../../Services/account.service';
import { InvitationService } from '../../Services/Invitation/invitation.service';

@Component({
	selector: 'app-statistics',
	templateUrl: './statistics.component.html',
	styleUrls: ['./statistics.component.scss'],
	providers: [
		StatisticsService,
		ProfilesService,
		AccountService,
		InvitationService
	]
})
export class StatisticsComponent implements OnInit {

	account 		: Object
	profile 		: Object
	statistics 		: Object
	// inviteId 		: string
	email_err 		: boolean = false
	emailsEntrance 	: string
	buttonText 		: string = 'Send Invitation'

	constructor(private StatisticsService: StatisticsService,
				private TokenService: TokenService,
				private ProfilesService: ProfilesService,
				private AccountService: AccountService,
				private InvitationService: InvitationService,
				private dialogService: DialogService) {
		this.account = this.TokenService.getToken().user
	}

	ngOnInit() {
		this.getUserProfile();
		// this.getUserInviteId();
		this.getUserStatistics();
	}

	getUserProfile() {
		this.ProfilesService.getProfile(this.account['id']).subscribe( res => {
			this.profile = res.profile;
		});
	}

	// getUserInviteId() {
	// 	this.AccountService.getUserInviteId(this.account['id']).subscribe( res => {
	// 		this.inviteId = res.invites[0];
	// 	});
	// }

	showSocialModal(index) {
		let disposable 	= this.dialogService.addDialog(GoogleContactModalComponent, {
        	profile 	: {},
        	index 		: index
    	}).subscribe( success => {
    		console.log(success);
    	});
	}

	showInvitationModal() {
		let disposable 	= this.dialogService.addDialog(InvitationSocialComponent, {
        	check : true
    	}).subscribe( success => {

    	});
	}

	getUserStatistics() {
		this.StatisticsService.getUserStatistics(this.account['id']).subscribe( res => {
			this.statistics = res.profile
		})
	}

	parsingEmails() {
		const reg 		= /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
		let emails 		= this.emailsEntrance.replace(/\s+/g, '').split(',');
		let length 		= emails.length
		let newMails 	= []

		for (let i = 0; i < length; i++) {
			if (reg.test(emails[i])) {
				newMails.push(emails[i]);
			}
		}

		return newMails
	}

	resetButton() {
		this.buttonText 		= 'Sent'
		this.emailsEntrance		= ''
		setTimeout( () => {
			this.buttonText = 'Send invites'
		}, 2000);
	}

	sendInvitations() {
		console.log(this.parsingEmails());
		if (this.emailsEntrance) {
			this.InvitationService.sendInvitation({ mail: this.parsingEmails() }).subscribe( res => {
				console.log(res);
				if (res.success) {
					this.resetButton();
				}
			});
		}
	}

}



